import { useState, useEffect } from "react";
import { Box, Stack, TextField, Typography, Button } from "@mui/material";
import {exerciseOption, fetchData} from '../utils/fetchData'
import HorizontalScrollbar from './HorizontalScrollbar'



const SearchExercises = ({setExercises, bodyPart, setBodyPart}) => {
  
  const [search, setSearch] = useState('')
  const [bodyParts, setBodyParts] = useState([])
  
  const handleSearch = async() => {
    if(search) {
      const exercisesData = await fetchData('https://exercisedb.p.rapidapi.com/exercises', exerciseOption);
      
      const searchedExercises = exercisesData.filter((exercise) => exercise.name.toLowerCase().includes(search)
      || exercise.target.toLowerCase().includes(search)
      || exercise.equipment.toLowerCase().includes(search)
      || exercise.bodyPart.toLowerCase().includes(search)
      );
      setSearch('')
      setExercises(searchedExercises)
    //  console.log(searchedExercises)
      
    }
  }
  useEffect(() => {
    const fetchExercisesData = async () => {
      const bodyPartsData = await await fetchData('https://exercisedb.p.rapidapi.com/exercises/bodyPartList', exerciseOption)
      setBodyParts(['all', ...bodyPartsData])
    }
    
    fetchExercisesData();
  }, [])
  
  //console.log(bodyParts)
  return (
    <Stack alignItems="center" mt="37px" justifyContent="center" p="20px">
      <Typography
        fontWeight={700}
        sx={{ fontSize: { lg: "44px", xs: "30px" } }}
        mb="50px"
        textAlign="center"
      >
        Awesome Exercise You <br />
        Should Know
      </Typography>
      <Box position="relative" mb="72px">
        <TextField
          sx={{
            input: {
              fontWeight: "700px",
              border: "none",
              borderRadius: "4px",
            },
            width: { lg: "800px", xs: "350px" },
            backgroundColor: "fff",
            borderRadius: "40px",
          }}
          height="76px"
          value={search}
          onChange={(e) => {setSearch(e.target.value.toLowerCase())}}
          placeholder="Search Exercises"
          type="text"
        />
        <Button
          className="search-btn"
          sx={{
            bgcolor: "#FF2625",
            color: "#fff",
            textTransform: "none",
            width: { lg: "175px", xs: "80px" },
            fontSize: { lg: "20px", xs: "14px" },
            height: "56px",
            position: "absolute",
            right: "0",
          }}
          onClick={handleSearch}
        >
          Search
        </Button>
      </Box>
      <Box 
        sx={{
          position:'realtive',
          width:'100%',
          padding:'20px'
        }}
      > 
         <HorizontalScrollbar data={bodyParts}
         bodyPart = {bodyPart}
         setBodyPart = {setBodyPart}
         />
      </Box>
    </Stack>
  );
};
export default SearchExercises;
